.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 9%;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 9999;
  background: #fff;
  box-shadow: var(--box-shadow);
}
.header .logo {
  font-size: 2.5rem;
  font-weight: bold;
  color: var(--black);
}
.header .logo i {
  color: var(--orange);
  margin-right: 0.5rem;
}
.header .icons button {
  height: 4.5rem;
  width: 4.5rem;
  line-height: 4.5rem;
  margin-left: 0.5rem;
  cursor: pointer;
  text-align: center;
  font-size: 2rem;
  background: #eee;
  border-radius: 0.5rem;
}
.header .icons button .fa-icon {
  color: var(--black);
  transition-duration: 0s;
}
.header .icons button:hover .fa-icon {
  color: #fff;
}
.header .icons button:hover {
  background: var(--orange);
}
.header .icons #menu-btn {
  display: none;
}
.badge {
  padding-left: 9px;
  padding-right: 9px;
  -webkit-border-radius: 9px;
  -moz-border-radius: 9px;
  border-radius: 9px;
}

.label-warning[href],
.badge-warning[href] {
  background-color: #c67605;
}
#lblCartCount {
    font-size: 12px;
    background: #ff0000;
    color: #fff;
    padding: 0 6px;
    vertical-align: top;
    margin-left: -10px; 
}